import debounce from 'lodash.debounce';
import Notiflix from 'notiflix';
import { fetchCountries } from './js/fetchCountries';

const input = document.querySelector('#search-box');
const lista = document.querySelector('.country-list');
const DEBOUNCE_DELAY = 500;

const handleSearchCountry = async (e) => {
    const inputPais = e.target.value.trim();
    lista.innerHTML = '';

    if (!inputPais) return;

    try {
        const data = await fetchCountries(inputPais);
        handleCountryData(data);
    } catch (error) {
        Notiflix.Notify.failure('Oops, there is no country with that name');
    }
};

const handleCountryData = (data) => {
    if (data.length > 10) {
        Notiflix.Notify.info('Too many matches found. Please enter a more specific name.');
    }

    if (data.length >= 2 && data.length <= 10) {
        const elementos = data.map((country) => {
            return `
        <li class="country-elements">
          <div class="country-img">
            <img src="${country.flags.png}" alt="" width="80px">
          </div>
          <div class="country-content">
            <h2>${country.name.common}</h2>
          </div>
        </li>
      `;
        }).join('');

        lista.insertAdjacentHTML('beforeend', elementos);
    }

    if (data.length === 1) {
        const [country] = data;
        const languages = Object.values(country.languages).join(', ');
        const countryInfo = `
      <li class="country-element">
        <div class="img-text">
          <img class="country-limg"src="${country.flags.png}" width="80px"> 
          <h2>${country.name.common}</h2>
        </div>

        <p><b>Capital:</b> ${country.capital}</p>
        <p><b>Population:</b> ${country.population}</p>
        <p><b>Languages:</b> ${languages}</p>
      </li>
    `;
        lista.insertAdjacentHTML('beforeend', countryInfo);
    }
};

input.addEventListener('input', debounce(handleSearchCountry, DEBOUNCE_DELAY));